.header {
    width: 100%;
    height: 93px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    left: 0;
    top: 0;
    margin-left: 50%;
    transform: translateX(-50%);
    z-index: 10;
    background-color: white;
    padding-left: 4rem;
    padding-right: 4rem;
}